import {
  Card,
  CardActionArea,
  CardActions,
  CardContent,
  CardMedia,
  createStyles,
  Link,
  makeStyles,
  Typography,
} from "@material-ui/core"
import { graphql } from "gatsby"
import React from "react"
import RepositoryTopicConnection, {
  RepositoryTopicConnectionProps,
} from "./repositoryTopicConnection"

const useStyles = makeStyles(
  (theme) => {
    return createStyles({
      root: {
        maxWidth: 345,
      },
      media: {
        height: 140,
      },
    })
  },
  { index: 1 }
)

export type RepositoryProps = {
  description: string | null
  id?: string
  name: string
  openGraphImageUrl: string
  repositoryTopics: RepositoryTopicConnectionProps
  url: string
  usesCustomOpenGraphImage: boolean
}

const Repository = (props: RepositoryProps) => {
  const classes = useStyles()
  return (
    <Link
      className={classes.root}
      href={props.url}
      rel="noopener noreferrer"
      target="_blank"
      underline="none"
    >
      <Card>
        <CardActionArea>
          {props.usesCustomOpenGraphImage && (
            <CardMedia
              alt={props.name}
              className={classes.media}
              component="img"
              height="140"
              image={props.openGraphImageUrl}
              title={props.name}
            />
          )}
          <CardContent>
            <Typography component="h2" gutterBottom variant="h5">
              {props.name}
            </Typography>
            <Typography color="textSecondary" component="p" variant="body2">
              {props.description}
            </Typography>
          </CardContent>
          <CardActions>
            <RepositoryTopicConnection nodes={props.repositoryTopics.nodes} />
          </CardActions>
        </CardActionArea>
      </Card>
    </Link>
  )
}

export default Repository

export const query = graphql`
  fragment RepositoryFragment on GitHub_Repository {
    description
    id
    name
    openGraphImageUrl
    repositoryTopics(first: 10) {
      ...RepositoryTopicConnectionFragment
    }
    url
    usesCustomOpenGraphImage
  }
`
