import {
  Avatar,
  Card,
  CardContent,
  CardHeader,
  createStyles,
  makeStyles,
  Typography,
} from "@material-ui/core"
import { graphql } from "gatsby"
import React from "react"
import PinnableItemConnection, {
  PinnableItemConnectionProps,
} from "./pinnableItemConnection"

const useStyles = makeStyles(
  (theme) => {
    return createStyles({
      root: {
        display: "flex",
        flexDirection: "column",
      },
    })
  },
  { index: 1 }
)

export type UserProps = {
  avatarUrl: string
  bio: string | null
  login: string
  name: string | null
  pinnedItems: PinnableItemConnectionProps
  url: string
}

const User = (props: UserProps) => {
  const classes = useStyles()
  return (
    <Card className={classes.root} elevation={0}>
      <CardHeader avatar={<Avatar alt={props.login} src={props.avatarUrl} />} />
      <CardContent>
        <Typography component="h2" variant="h5">
          {props.name}
        </Typography>
        <Typography color="textSecondary" component="p" variant="body2">
          {props.bio}
        </Typography>
      </CardContent>
      <PinnableItemConnection nodes={props.pinnedItems.nodes} />
    </Card>
  )
}

export default User

export const query = graphql`
  fragment UserFragment on GitHub_User {
    avatarUrl
    bio
    login
    name
    pinnedItems(first: 6) {
      ...PinnableItemConnectionFragment
    }
    url
  }
`
